/* 此為用以與Native APP溝通的介面定義部分
 * 自行定義的 Schema 格式如下：
 * MTKSchema://?MTKServiceInfo.ServiceHeader?ServiceID=FMI102H@@@AAA=123@@@BBB=333&MTKServiceInfo.FMI102HURL
 * ● Schema 必為 MTKSchema。
 * ● 第一個 ? 後面代表 js 需要 App 協助處理的動作代碼，若有多個則以 & 符號串接，
 *   如上述範例會依序執行「MTKServiceInfo.ServiceHeader」及「MTKServiceInfo.FMI102HURL」的動作。
 * ● 若 js 需要帶給每個動作特定參數值時，加入 ? 符號，並以「@@@」串接、分隔多個參數值 (Key=Value)。
 */
var App = (function () {
  return {
    "GoBack":function(){
      location.href = "MTKSchema://?GoBack";
    },
    "GoHome":function(){
      location.href = "MTKSchema://?GoHome";
    },
    "OpenFile":function(filepath){
      location.href = "MTKSchema://?OpenFile?path=" + filepath;
    },
    "ShowAlert":function(message,title,buttonText,callback){
      var titleParam = (title && title.length > 0) ? "@@@title=" + title : "",
          buttonTextParam = (buttonText && buttonText.length > 0) ? "@@@buttonText=" + buttonText : "";
      var callbackParam = (callback && (callback+"").length > 0) ? "@@@callback=" + callback+"" : "";
      var schema = "MTKSchema://?ShowAlert" + 
                    "?message=" + 
                    message + 
                    titleParam + 
                    buttonTextParam + 
                    callbackParam;
      console.log(schema);
      location.href = schema;
    },
    "StartLoading":function(text){
      var textParam = (text && text.length > 0) ? "?text=" + text : "";
      location.href = "MTKSchema://?StartLoading" + textParam;  
    },
    "StopLoading":function(){
      location.href = "MTKSchema://?StopLoading";
    },
    "ShowVersion":function(){
      location.href = "MTKSchema://?ShowVersion?v=" + Constants.appWebVersion;
    },
    "AddFavorite":function(fundcode){
      var schema = "MTKSchema://?AddFavorite?fundcode=" + fundcode;
      console.log(schema);
      location.href = schema;
    },
    "RemoveFavorite":function(fundcode){
      var schema = "MTKSchema://?RemoveFavorite?fundcode=" + fundcode;
      console.log(schema);
      location.href = schema;
    },
    "ListFavorite":function(){
      var schema = "MTKSchema://?ListFavorite";
      console.log(schema);
      location.href = schema;
    }    
  };
})();

$(function() {
    var backBtn = $("#btn-back");
    var homeBtn = $("#btn-home");

    App.ShowVersion();

    if(backBtn){
      backBtn.on("vclick",function(){
        App.GoBack();
      });
    }

    if(homeBtn){
      homeBtn.on("vclick",function(){
        App.GoHome();
      });
    }
});
